import React, { FC, useEffect, useState } from 'react';
import { FieldErrors, FieldValues, SubmitHandler } from 'react-hook-form';
import FormErrors from '@components/Errors/FormErrors';
import Button from '@components/ui/Button';
import isEmpty from '@helpers/isEmpty';
import { APP_URL, PRIVACY_POLICY_TEXT } from '@constants';
import './Form.scss';

interface FormProps {
  children: JSX.Element | React.ReactNode;
  errors: FieldErrors;
  close: () => void;
  cancel: () => void;
  onSubmit: any; // Todo
  submitText?: string;
  showPrivacyPolicy?: boolean;
  text?: string;
}

const Form: FC<FormProps> = ({
  children,
  errors,
  cancel,
  onSubmit,
  submitText = '',
  text,
  showPrivacyPolicy = false,
}) => {
  const [privacyPolicy, setPrivacyPolicy] = useState<boolean>(false);
  const hasErrors = !isEmpty(errors);

  return (
    <form onSubmit={onSubmit} className="form">
      <p className="form__text">{text}</p>

      {children}

      <FormErrors errors={errors} />

      {showPrivacyPolicy ? (
        <div className="form__privacy-policy">
             {submitText},  &nbsp;
          <a href="#" rel="noreferrer" onClick={() => setPrivacyPolicy(true)}>
                
          </a>
          {privacyPolicy ? (
            <div className="form__privacy-policy-modal">
              {PRIVACY_POLICY_TEXT}

              <Button
                type="button"
                className="form__privacy-policy-modal-btn"
                clickHandler={() => setPrivacyPolicy(false)}
              >
                
              </Button>
            </div>
          ) : null}
        </div>
      ) : null}

      <div className="form__btn-wrapper">
        <Button
          className="form__btn-cancel cancel"
          type="reset"
          clickHandler={cancel}
        >
          
        </Button>

        <Button className="form__btn-submit" type="submit" disabled={hasErrors}>
          {submitText}
        </Button>
      </div>
    </form>
  );
};

export default Form;
